<template>
  <div class="big-table-wrap" ref="tableWrap">
        <div class="search-wrap" ref="searchWrap">
          <Row :gutter="16">
            <Col span="6">
              <div  class="input-item">
                  <span class="input-label">用户账号：</span>
                   <Input v-model="adminID" placeholder="账户1ID" class="input-width"></Input>
              </div>
            </Col>
            <Col span="6">
              <div class="input-item">
                  <span class="input-label"> 用户名称：</span>
                  <Input v-model="adminName" placeholder="账户名称" class="input-width"></Input>
              </div>
            </Col>
            <Col span="6">
              <div class="input-item">
                <span class="input-label"> 关联角色：</span>
                <Input v-model="adminActor" placeholder="账户名称" class="input-width"></Input>
              </div>
            </Col>
            <Col span="6">
              <div class="input-item">
                <span class="input-label"> 登录密码：</span>
                <Input v-model="adminPSW" placeholder="账户名称" class="input-width"></Input>
              </div>
            </Col>
          </Row>
          <Row :gutter="16">
            <Col span="6">
              <div  class="input-item">
                <span class="input-label">创建时间：</span>
                <DatePicker v-model="creatTime" type="daterange" @on-change="getCreatTime"  placement="bottom-start" placeholder="创建时间" class="input-width"></DatePicker>
              </div>
            </Col>
            <Col span="6">
              <div  class="input-item">
                <span class="input-label">更新时间：</span>
                <DatePicker  type="daterange" @on-change="getUpdateTime" placement="bottom-start" placeholder="更新时间" class="input-width"></DatePicker>
              </div>
            </Col>
            <Col span="4">
              <div  class="input-item">
                <span class="input-label">状态：</span>
                <i-switch  v-model="statueSwitch" size="large" @on-change="getSatue" style="margin-top:3px;">
                  <span slot="open">开启</span>
                  <span slot="close">禁用</span>
                </i-switch>
              </div>
            </Col>
            <Col span="4">
              <div  class="input-item">
                <Button type="primary" @click="searchInfo">查询</Button>
              </div>
            </Col>
          </Row>
        </div>
      <!--操作按钮区域-->
       <div class="handle-wrap" ref="handleWrap">
           <div class="handle-btn-wrap">
             <Button type="primary" @click="exportInfo">导出</Button>
           </div>
          <div class="handle-btn-wrap">
            <Button type="primary" @click="addInfoWrapShow">新增</Button>
          </div>
       </div>
       <!--table表格区域-->
      <div class="table-wrap-style" ref="tableBox">
        <Table  :loading="loading" :height="tableHeight" :data="tableData" :columns="tableColumns"  stripe border></Table>
      </div>
      <div class="page-wrap" ref="pageWrap">
        <div class="right">
          <Page :total="total" :current="page" @on-change="changePage" @on-page-size-change="changeSize" show-total show-sizer :page-size-opts="pageSizeOpts"></Page>
        </div>
      </div>
      <!--新增信息弹窗-->
      <Modal v-model="addWrap" title="新增信息"   width="560">
        <Form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="80">
          <Form-item label="用户账号" prop="account">
            <Input v-model="formValidate.account" placeholder="" ></Input>
          </Form-item>
          <Form-item label="用户名称" prop="name">
            <Input v-model="formValidate.name" placeholder="" ></Input>
          </Form-item>
          <Form-item label="密码" prop="passWord">
            <Input v-model="formValidate.passWord" placeholder="" ></Input>
          </Form-item>
          <Form-item label="确认密码" prop="repassWord">
            <Input v-model="formValidate.repassWord" placeholder="" ></Input>
          </Form-item>
          <Form-item label="是否启用" >
            <i-switch v-model="formValidate.statue" size="large" @on-change="getSatueInWrap" style="margin-top:3px;">
              <span slot="open">开启</span>
              <span slot="close">禁用</span>
            </i-switch>
          </Form-item>
          <Form-item label="关联角色" >
            <Transfer
              :data="transferData"
              :titles="['所有角色','选中角色']"
              :target-keys="targetKeys"
              :render-format="transferRender"
              @on-change="handleChange"></Transfer>
          </Form-item>
        </Form>
        <div slot="footer">
          <Button @click="addWrap=false">取消</Button>
          <Button  type="primary" :loading="addLoding" @click="addInfoSave">确定</Button>
        </div>
      </Modal>
      <!--修改信息的弹窗-->
      <Modal v-model="updateWrap" title="修改信息" width="560">
        <Form ref="updateFormValidate" :model="updateFormValidate" :rules="ruleValidate" :label-width="80">
          <Form-item label="账号" prop="account">
            <Input v-model="updateFormValidate.account" placeholder="" ></Input>
          </Form-item>
          <Form-item label="重置密码" prop="passWord">
            <Input v-model="updateFormValidate.passWord" placeholder="" ></Input>
          </Form-item>
          <Form-item label="是否启用" >
            <i-switch v-model="updateFormValidate.statue" size="large" @on-change="getSatueInUpdateWrap" style="margin-top:3px;">
              <span slot="open">开启</span>
              <span slot="close">禁用</span>
            </i-switch>
          </Form-item>
          <Form-item label="关联角色" >
            <Transfer
              :data="transferData"
              :titles="['所有角色','选中角色']"
              :target-keys="updateTargetKeys"
              :render-format="transferRender"
              @on-change="handleUpdateChange"></Transfer>
          </Form-item>
        </Form>
        <div slot="footer">
          <Button @click="updateWrap=false">取消</Button>
          <Button  type="primary" :loading="updateLoading" @click="updateInfoSave">确定</Button>
        </div>
      </Modal>
    </div>
</template>
<script>
  import { tableMethods } from "../../../js/mixintable";
    export default {
        data () {
            return {
              /*搜索区域 条件*/
                adminID:'', //账号id
                adminName:'', //账号名称
                adminActor:'', //关联角色
                adminPSW:'', //账号密码
                statueSwitch:true,//状态 true false
                creatTime:'', //创建时间
                uptateTime:'', //更新时间
              /*新增弹窗*/
                addWrap:false, //添加弹窗
                addLoding:false,
                transferData: this.getTransferData(),
                targetKeys: [],
                formValidate:{
                  account:'',
                  passWord:'',
                  name:'',
                  repassWord:'',
                  statue:true,
                },
              /*修改弹窗*/
                updateWrap:false, //修改弹窗
                updateLoading:false,
                updateTargetKeys:this.getTargetKeys(),
                updateFormValidate:{
                  account:'',
                  passWord:'',
                  statue:true,
                },
              ruleValidate: {
                account: [
                  { required: true, message: '账号不能为空', trigger: 'blur' }
                ],
                name: [
                  { required: true, message: '名称不能为空', trigger: 'blur' }
                ],
                passWord: [
                  { required: true, message: '密码不能为空', trigger: 'blur' }
                ],
                repassWord: [
                  { required: true, message: '确认密码不能为空', trigger: 'blur' }
                ],
              },
              /*table表格*/
                tableData: [
                  {companyname:'银点acount',userTitle:"admin",creat_time:'2018-2-1',update_time:'2018-2-1',user_role:'角色1',"isEnable":'是'},
                  {companyname:'快乐农家acount',userTitle:"admin2",creat_time:'2018-2-1',update_time:'2018-2-1',user_role:'角色11',"isEnable":'否'},
                  {companyname:'银点acount',userTitle:"admin3",creat_time:'2018-2-1',update_time:'2018-2-1',user_role:'角色12',"isEnable":'是'}
                ],
                total: 0,
                page: 1,
                size:10,
                tableHeight:200,
                loading:false,
                tableColumns: [
                    {
                        title: '用户账号',
                        key: 'companyname',
                        align: 'center',
                    },
                    {
                        title: '用户名称',
                        align: 'center',
                        key: 'userTitle',
                    },
                    {
                        title: '创建时间',
                        align: 'center',
                        key: 'creat_time'
                    },
                    {
                        title: '更新时间',
                        align: 'center',
                        key: 'update_time'
                    },
                    {
                        title: '关联角色',
                        key: 'user_role',
                        align: 'center',
                       /* render: function(row, column, index){
                               return row.sex == 0 ? '男' : '女';
                        }*/
                    },
                    {
                        title: '是否启用',
                        align: 'center',
                        key: 'isEnable'
                    },
                     {
                        title: '操作',
                        key: 'action',
                        width: 250,
                        align: 'center',
                        render: (h, params) => {
                            return h('div', [
                                h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            this.show(params.index)
                                        }
                                    }
                                }, '查看'),
                                h('Button', {
                                  props: {
                                    type: 'primary',
                                    size: 'small'
                                  },
                                  style: {
                                    marginRight: '5px'
                                  },
                                  on: {
                                    click: () => {
                                      this.updateInfo(params.index)
                                    }
                                  }
                                }, '编辑'),
                                h('Button', {
                                    props: {
                                        type: 'error',
                                        size: 'small'
                                    },
                                    on: {
                                        click: () => {
                                            this.remove(params.index)
                                        }
                                    }
                                }, '删除')
                            ]);
                        }
                    }
                ]
            }
        },
      mixins: [tableMethods],
        mounted() {
          this.$nextTick(function () {
                  //dom更新了，重新设置table的高度
                  // this.tableHeight=this.$refs.tableWrap.clientHeight-this.$refs.searchWrap.clientHeight-this.$refs.handleWrap.clientHeight-this.$refs.pageWrap.clientHeight-3
                })
           // this.loadData();
        },
        methods: {
          //暂时请求推荐商家
          loadData() {
            var param = new Object();
            param["query_type"] = "Pages";
            param["pPage"] = this.page;
            param["pSize"] = this.size;
            this.$Loading.start();
            this.loading=true;

            let paramData = protocol.getJsonParams(protocol.RecommendMerchant, param);
            http.ajax('', paramData, true, (data) => {

              this.loading=false;
              this.$Loading.finish();
              this.tableData = data.CooperationSeller;
              this.total =data.total-0;
            })
          },
          //根据查询条件进行数据的查询
          searchInfo(){
            this.loadData();
            console.log(this.adminID)
            console.log(this.adminName)
            console.log(this.statueSwitch)
            console.log(this.creatTime)

          },
          //请求个人数据并展示
            show (index) {
                this.$Modal.info({
                    title: '用户信息',
                    content: `姓名：${this.tableData[index].companyname}<br>年龄：${this.tableData[index].companyname}<br>地址：${this.tableData[index].companyname}`
                })
            },
          //添加数据的弹窗的显示
          addInfoWrapShow(){
              this.addWrap=true
            /*初始化数据*/
              this.formValidate={
                account:'',
                passWord:'',
                name:'',
                repassWord:'',
                statue:true,
             }
          },
         /* 删除数据*/
            remove (index) {
                 let self = this;
                 this.$Modal.confirm({
                    title: '用户信息',
                    content: `是否删除此记录`,
                    onOk: function(){
                        this.$Loading.start();
                        let para = {id: index}
                         removeUser(para).then((res)=> {
                            self.mockTableData();
                      });
                    }
                })
            },
          //验证保存添加信息
          addInfoSave() {
              this.addLoding=true
           this.$refs.formValidate.validate((valid) => {
              if (valid) {
                if(this.formValidate.passWord!=this.formValidate.repassWord){
                  this.$Message.error('两次输入的密码不同!')
                  this.addLoding=false
                  return
                }
                setTimeout(() => {
                  this.addWrap=false
                  this.addLoding=false
                  this.$Message.success('提交成功!');
                }, 2000);
              } else {
                this.$Message.error('表单验证失败!');
                this.addLoding=false
                return false;
              }
            })
          },
          //修改页码 ，重新加载数据
            changePage (row) {
                // 这里直接更改了模拟的数据，真实使用场景应该从服务端获取数据
                this.page = row;
                this.loadData();
            },
          //获取到当前的状态
          getSatue(e){
            this.statueSwitch=e
          },
          //获取创建时间
          getCreatTime(e){
              this.creatTime=e
            console.log(e)
          },
          //获取更新时间
          getUpdateTime(e){
             this.uptateTime=e
          },
          //获取当前新增框中的状态
          getSatueInWrap(e){
            this.formValidate.statue=e
            console.log(this.formValidate)
          },
          //获取穿梭框中的数据
          getTransferData () {
            let mockData = [];
            for (let i = 1; i <= 20; i++) {
              mockData.push({
                key: i.toString(),
                label: '角色 ' + i,
                description: '角色' + i,
              });
            }
            return mockData;
          },
          /*获取穿梭框中---选中的数据*/
          getTargetKeys () {
            return this.getTransferData()
              .filter(() => Math.random() * 2 > 1)
              .map(item => item.key);
          },
          //设置要展示的信息字段
          transferRender (item) {
            return item.description;
          },
          //新增弹窗-------穿梭框的的数据变化
          handleChange (newTargetKeys, direction, moveKeys) {
            this.targetKeys = newTargetKeys;
          },
          //修改弹窗------穿梭框的的数据变化
          handleUpdateChange (newTargetKeys, direction, moveKeys) {
            this.updateTargetKeys = newTargetKeys;
          },
          //展示修改弹窗
          updateInfo(){
            this.updateWrap=true
            /*初始化数据*/
           this.updateFormValidate={
                account:'',
                passWord:'',
                statue:true,
            }
            //获取该人员的详细信息，并设置值
          },
          //获取修改穿梭框中的状态
          getSatueInUpdateWrap(e){
            this.updateFormValidate.statue=e
          },
          //保存修改管理员信息
          updateInfoSave(){
            this.updateLoading=true
            this.$refs.updateFormValidate.validate((valid) => {
              if (valid) {
                this.updateWrap=false
                this.updateLoading=false
                this.$Message.success('提交成功!');
              } else {
                this.$Message.error('表单验证失败!');
                this.updateLoading=false
                return false;
              }
            })
          },
          //导出数据
          exportOk(){
            console.log(11)
            /*没有的代码  仅仅是暂时*/
            var form = document.createElement('form')
            form.enctype = 'application/x-www-form-urlencoded'
            form.method = 'POST'
            form.action = baseUrl + '/pc/information/exportInfo.do'
            var map = {
              'token': that.token,
              'strApp': that.strApp,
              'strInfoNo': that.strInfoNo, //
              'isCash': that.isCash, // 全部   是 否
              'strContent': that.strContent, // 内容
            }

            for (var i in map) {
              var f = document.createElement('input')
              f.type = 'hidden'
              f.name = i
              f.value = map[i]
              form.appendChild(f)
            }
            document.body.appendChild(form)
            form.submit()
            document.body.removeChild(form)
            form = null
          },
          //导出数据
          exportInfo(){
            let that=this
            this.$Modal.confirm({
              title: '提示',
              content: `确定导出吗？`,
              onOk: function(){
                that.exportOk()
              }
            })
          },
        },
    }
</script>
<style lang="less" scoped>
  @import '../../../css/common.css';
</style>



